<template>
  <div class="parameter">
    <div class="content-title">
      
    </div>
    <div class="include">
      <div class="info">
        <div class="title"></div>
        <Row>
          <Col span="5" class="assetType">
            <span></span>
            <span>{{queryData.thingCode}}</span>
          </Col>
          <Col span="8" class="assetType">
          <span></span>
          <span>{{queryData.thingName}}</span>
          </Col>
          <Col span="5" class="assetType">
          <span></span>
          <span>{{queryData.metricType}}</span>
          </Col>
          <Col span="6" class="assetType">
          <span></span>
          <span>{{queryData.metricName}}</span>
          </Col>
        </Row>
      </div>
      <div class="info">
        <div class="title"></div>
        <div class="slider">
          <div class="alt" v-if="queryData.alertRules.length == 0"></div>
          <div class="section" v-if="queryData.alertRules.length > 0" v-for="section in queryData.alertRules" :style="section.style" @click="section.popVisible = !section.popVisible">
            <Poptip v-model="section.popVisible" placement="top">
              <div slot="content">
                <div class="item" @click="editSection(section)"></div>
                <div class="item" @click="delSection(section)"></div>
              </div>
            </Poptip>
            <span class="left">{{section.lowerLimit}}</span>
            <span class="right">{{section.upperLimit}}</span>
          </div>
          <Slider v-model="sliderRange" :min="minimum" range :max="maximum" disabled></Slider>
          <div class="value">
            <div class="min">
              <input type="text" placeholder="" v-model="minimum" @change="changeSlider()">
            </div>
            <div class="max">
              <input type="text" placeholder="" v-model="maximum" @change="changeSlider()">
            </div>
          </div>
          <div class="sections">
            <div class="title"></div>
            <div class="limits">
              <div class="limit">
                <input type="text" name="lowerLimit" v-model="lowerLimit" placeholder="" v-validate="'max_value:120'">
                <span v-show="errors.has('lowerLimit')" class="help is-danger"></span>
              </div>
              <div class="connector"></div>
              <div class="limit">
                <input type="text" v-model="upperLimit" placeholder=""  v-validate="'min_value:2000'">
                <span v-show="errors.has('upperLimit')" class="help is-danger"></span>
              </div>
              <div class="clearfix"></div>
            </div>
          </div>
          <div class="sections">
            <div class="title"></div>
            <div class="alertLevels">
              <RadioGroup v-model="alertLevel" :on-change="changeLevel()" type="button">
                <Radio label="" :class="alertLevel==''?'active':''"></Radio>
                <Radio label="" :class="alertLevel==''?'active':''"></Radio>
                <Radio label="" :class="alertLevel==''?'active':''"></Radio>
              </RadioGroup>
            </div>
          </div>
        </div>
        <div class="buttons">
          <div class="button" @click="cancel()">
            
          </div>
          <div class="button" @click="remain()">
            
          </div>
          <div class="button" @click="goBack()">
            
          </div>
          <div class="clearfix"></div>
        </div>
      </div>
    </div>
    <Modal
      title="title"
      width="430"
      v-model="toastModel"
      :mask-closable="false"
      class-name="small-modal"
      :closable="false">
      <p slot="header">
        <span></span>
        <span class="titleLeft" @click="closeToast()">
          <img src="src/images/warning/webqt_iocn.png" alt="close">
        </span>
      </p>
      <p>
        <span>"[{{currentSection.lowerLimit}},{{currentSection.upperLimit}}]"?</span>
      </p>
      <div slot="footer">
        <Button type="text" size="large" @click="closeToast()"></Button>
        <Button type="primary" size="large" @click="confirmToast()"></Button>
      </div>
    </Modal>
    <Modal
      title="title"
      width="430"
      v-model="editModel"
      :mask-closable="false"
      class-name="edit-modal"
      :closable="false">
      <p slot="header">
        <span></span>
        <span class="titleLeft" @click="close()">
          <img src="src/images/warning/webqt_iocn.png" alt="close">
        </span>
      </p>
      <div class="sections">
        <div class="title"></div>
        <div class="limits">
          <div class="limit">
            <input type="text" name="lowerLimit" v-model="editSec.lowerLimit" placeholder="" v-validate="'max_value:120'">
            <span v-show="errors.has('currentSection.lowerLimit')" class="help is-danger"></span>
          </div>
          <div class="connector"></div>
          <div class="limit">
            <input type="text" v-model="editSec.upperLimit" placeholder=""  v-validate="'min_value:2000'">
            <span v-show="errors.has('currentSection.upperLimit')" class="help is-danger"></span>
          </div>
          <div class="clearfix"></div>
        </div>
      </div>
      <div class="sections">
        <div class="title"></div>
        <div class="alertLevels">
          <RadioGroup v-model="editSec.alertLevel" :on-change="changeLevel()" type="button">
            <Radio label="" :class="editSec.alertLevel==30?'active':''"></Radio>
            <Radio label="" :class="editSec.alertLevel==20?'active':''"></Radio>
            <Radio label="" :class="editSec.alertLevel==10?'active':''"></Radio>
          </RadioGroup>
        </div>
      </div>
      <div slot="footer">
        <Button type="text" size="large" @click="closeEdit()"></Button>
        <Button type="primary" size="large" @click="confirmEdit()"></Button>
      </div>
    </Modal>
  </div>
</template>
<style></style>
<script>
  export default{
    name: 'parameter',
    data: () => ({
      minimum:0,
      maximum:300,
      queryData: {},
      sliderRange:[this.minimum,this.maximum],
      lowerLimit:null,
      upperLimit:null,
      alertLevel:null,
      popVisible:false,
      toastModel:false,
      editModel:false,
      currentSection:{},
      editSec:{}
    }),
    created () {
      // 
      //  data  observed 
      this.ready()
    },
    methods: {
      /**
       * 
       */
      ready() {
        this.queryData = this.$route.params.obj;
        for (var i = 0; i < this.queryData.alertRules.length; i++) {
          var color;
          if(this.queryData.alertRules[i].alertLevel == 30){
            color = "rgb(253,238,0)";
          }else if(this.queryData.alertRules[i].alertLevel == 20){
            color = "rgb(180,16,248)";
          }else{
            color = "rgb(48,166,228)";
          }
          var unitLength = 668.8 / (this.maximum - this.minimum);
          this.queryData.alertRules[i].style = {
            width: unitLength *(this.queryData.alertRules[i].upperLimit - this.queryData.alertRules[i].lowerLimit)  +"px",
            left: unitLength * this.queryData.alertRules[i].lowerLimit + 83.6 +"px",
            backgroundColor : color
          }
        }
      },
      changeLevel() {
//          console.log(this.alertLevel);
      },
      /**
       * 
       */
      cancel() {
          var this_obj = this;
          this_obj.lowerLimit = null;
          this_obj.upperLimit = null;
          this_obj.alertLevel = null;
      },
      /**
       * 
       */
      remain() {
        var this_obj = this;
        var node = {};
        node.lowerLimit = this_obj.lowerLimit;
        node.upperLimit = this_obj.upperLimit;
        var color;
        if(this_obj.alertLevel == ''){
          color = "rgb(253,238,0)";
          node.alertLevel = 30;
        }else if(this_obj.alertLevel == ''){
            color = "rgb(180,16,248)";
          node.alertLevel = 20;
        }else{
            color = "rgb(48,166,228)";
          node.alertLevel = 10;
        }
        var unitLength = 668.8 / (this_obj.maximum - this_obj.minimum);
        node.style = {
            width: unitLength *(node.upperLimit - node.lowerLimit)  +"px",
            left: unitLength * node.lowerLimit + 83.6 +"px",
            backgroundColor : color
        }
        this_obj.queryData.alertRules.push(node);
        this_obj.lowerLimit = null;
        this_obj.upperLimit = null;
        this_obj.alertLevel = null;
      },
      /**
       * 
       */
      goBack(){
        this.$router.push({name: 'paraTypeAlert'})
      },
      /**
       * 
       */
      changeSlider(){
        var this_obj = this;
        this_obj.sliderRange = [this_obj.minimum,this_obj.maximum];
      },
      /**
       * 
       */
      delSection(section){
        this.currentSection = section;
        this.toastModel = true;
      },
      /**
       * 
       */
      editSection(section){
        this.currentSection = section;
        this.editSec = copyArr(section);
        this.editModel = true;
      },
      /**
       * 
       */
      confirmToast(){
          this.toastModel = false;
      },
      /**
       * 
       */
      closeToast(){
        this.toastModel = false;
      },
      /**
       * 
       */
      confirmEdit(){
        this.editModel = false;
      },
      /**
       * 
       */
      closeEdit(){
        this.editModel = false;
      }
    }
  }
  /**
   * 
   * @param arr
   */
  function copyArr(arr){
    return arr.map((e)=>{
      if(typeof e === 'object'){
        return Object.assign({},e)
      }else{
        return e
      }
    })
  }
</script>



